import React from 'react';
import styled from 'styled-components';
import Vimeo from '@u-wave/react-vimeo';
import Fade from 'react-reveal/Fade';
import { Container } from '@components/global';

const Video = ({ header, text, video, caption}) => (
  
  <Container>
    <HeaderWrapper>
      <Grid>
        <div/>
        <Fade>
        <TopTextMargin>
          <h3>{header}</h3>
          <p>{text}</p>
        </TopTextMargin>
        </Fade>
      </Grid>
      <Art>
        <Vimeo
          video={video}
          width="100%"
          responsive="true"
        />
        <Caption>{caption}</Caption>
      </Art>
    </HeaderWrapper>
  </Container>
);


const HeaderWrapper = styled.div`
  padding: 50px 0 50px 0;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0;
    margin-left: 0%;
  }
  ${props =>
    props.togglecover &&
    `padding: ${
      props.togglecover === 'false'
        ? '50px 0 50px 0;'
        : '0px 0px 0px 0px;'
    }`};
`;


const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
    margin-bottom: 96px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;


const TopTextMargin = styled.div`
  margin: 0 30% 50px 0;
  @media (max-width: ${props => props.theme.screen.md}) {
    margin: 0;
  }
`;

const Art = styled.figure`
  margin: 0;
  max-width: 1380px;
  width: 100%;
  left: 0;
  img{
    width: 100%;
  }
`;

const Caption = styled.div`
  padding-top:5px;
  padding-right: 30%;
  ${props => props.theme.font_size.xsmall};
`;


export default Video;
